      <div class="operation">
        <div class="operation-one" @click="showuppsd">
          <div class="one-text">修改密码</div>
          <div class="one-tu"><svg t="1666253167823" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="5211" width="200" height="200"><path d="M301.99808 44.9792l-47.04768 47.09888 419.9424 419.98848-419.9424 419.90144 47.0528 47.0528 419.93728-419.89632h0.00512l47.104-47.09888-47.0528-47.04768z" p-id="5212" fill="#515151"></path></svg></div>
