              <van-form>
                <van-field
                v-model="oldpsd"
                  type="password"
                  name="老密码"
                  colon
                  label="老密码"
                  placeholder="老密码"
                />
                <van-field
                  v-model="newpsd"
                  type="password"
                  colon
                  name="新密码"
                  label="新密码"
                  placeholder="新密码"
                />
                <div style="margin: 16px;">
                  <van-button round block type="info" native-type="submit" @click="updatepsd">提交</van-button>
                </div>
              </van-form>
            </van-popup>
        <div class="operation-two">
          <div class="two-text">联系我们</div>
          <div class="two-tu"><svg t="1666253167823" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="5211" width="200" height="200"><path d="M301.99808 44.9792l-47.04768 47.09888 419.9424 419.98848-419.9424 419.90144 47.0528 47.0528 419.93728-419.89632h0.00512l47.104-47.09888-47.0528-47.04768z" p-id="5212" fill="#515151"></path></svg></div>
        </div>
        <div class="operation-three">
          <div class="three-text">关于我们</div>
          <div class="three-tu"><svg t="1666253167823" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="5211" width="200" height="200"><path d="M301.99808 44.9792l-47.04768 47.09888 419.9424 419.98848-419.9424 419.90144 47.0528 47.0528 419.93728-419.89632h0.00512l47.104-47.09888-47.0528-47.04768z" p-id="5212" fill="#515151"></path></svg></div>
        </div>
      </div>
      <div class="foot" @click="outlogin">
        退出登录
      </div>
    </div>
    <div v-else>
      <div class="bgc">
          <img src="@/assets/hg.jpg" alt="">
      </div>
      <div class="t" @click="$router.push('/login')">
            请先登录
      </div>
    </div>
  </div>
</template>

<script>
import { Popup,Field,Form,Button, Toast } from 'vant'
import axios from 'axios'
export default {
components:{
    [Popup.name]:Popup,
    [Field.name]:Field,
    [Form.name]:Form,
    [Button.name]:Button
},
  data(){
    return{
      oldpsd:null,
      newpsd:null,
      show:false,
      isLogin:true,
      user:{userInfo:{nickname:''}},
      avatar:null
    }
  },
  methods:{
    showuppsd(){
      this.show = !this.show
    },
    // 修改密码
    updatepsd(){
      // console.log(this.oldpsd);
      // console.log(this.newpsd);
      axios({
        method:'post',
        url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/updatePwd',
        data:{
          oldPassword:this.oldpsd,
          newPassword:this.newpsd,
          uid:localStorage.getItem('uid')
        }
      }).then(res =>{
        console.log(res);
        Toast('更改成功,请重新登录')
        this.outlogin()
      })
    },
    outlogin(){
      localStorage.removeItem('Key')
      localStorage.removeItem('uid')
      localStorage.removeItem('token')
      localStorage.removeItem('tokenExpired')
      localStorage.removeItem('userInfo')
      localStorage.removeItem('nickname')
      localStorage.removeItem('avatar')
      localStorage.removeItem('birthday')
      localStorage.removeItem('sex')
      this.isLogin = false;
      this.$router.push('/index')
    },
    history(){
      this.$router.push('/history')
    },
    seehistory(){
      this.$router.push('/seehistory')
    }
  },
created(){
  axios({
    method:'post',
    url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/getuserInfo',
    data:{
      'token':localStorage.getItem('token')
    }
  }).then(res =>{
    if(res.data.msg=='获取用户信息成功'){
      console.log(res);
      this.isLogin = true
      this.user=res.data
    }else{
      this.isLogin = false
    }
    // console.log(res.data);
    let {nickname ,avatar,birthday,sex} = res.data.userInfo
    localStorage.setItem("nickname", nickname);
    localStorage.setItem("avatar", avatar);
    localStorage.setItem("birthday", birthday);
    localStorage.setItem("sex", sex);
  })
},
}
</script>

